import React from 'react'
import { Drawer, GridList, GridListTile, Tooltip, IconButton, Typography } from '@material-ui/core'
import { BookmarkBorder, ViewCarousel } from '@material-ui/icons'
import { Document, Page, pdfjs } from 'react-pdf'
import { Link } from 'react-router-dom'

// Styles
import classes from './BookmarkDrawer.module.scss'

pdfjs.GlobalWorkerOptions.workerSrc = `https://cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`
export default function BookmarkDrawer (props) {
  const {
    anchor = false,
    alternateFile,
    file,
    setNumPages,
    setPageNumber,
    isLandscape = false,
    rotate,
    numPages
    // bookmark
  } = props
  let indexPage = 2

  // const [isBookmark, setBookmark] = React.useState(false)
  // const [isThumbnail, setThumbnail] = React.useState(true)

  const tile = () => {
    const data = []
    const pages = []
    for (let i = 1; i <= numPages; i++) {
      data.push(
        <Document
          file={alternateFile ? alternateFile : file}
          onLoadSuccess={document => {
            setNumPages(document.numPages)
          }}
          className={classes.document}
        >
          <Page pageNumber={i} scale={0.5} rotate={rotate} />
        </Document>
      )
      pages.push(i)
    }
    return { data, pages }
  }

  const orientation = () => {
    if (isLandscape === false) {
      return {
        height: 435,
        width: '300px'
      }
    } else {
      return {
        height: 300,
        width: '430px'
      }
    }
  }

  const indexToPages = index => {
    if (index === 0) {
      return 1
    }
    if (index === 1) {
      return index + 1
    }
    if (index > 1) {
      return index++
    }
  }

  // const handleOnBookMark = () => {
  //   setBookmark(true)
  //   if (isThumbnail === true) setThumbnail(false)
  // }

  // const handleOnThumbnail = () => {
  //   setThumbnail(true)
  //   if (isBookmark === true) setBookmark(false)
  // }

  return (
    <div className={classes.root}>
      <Drawer
        drawerHeight={'900px'}
        variant='persistent'
        anchor={'left'}
        classes={{
          paper: classes['drawer-paper']
        }}
        className={classes['drawer-container']}
        open={anchor}
      >
        <div className={classes['drawer-root']}>
          <div className={classes['drawer-header']}>
            {/* <div className={classes.buttons}>
              <Tooltip title='Bookmark'>
                <IconButton onClick={() => handleOnBookMark()}>
                  <BookmarkBorder />
                </IconButton>
              </Tooltip>
              <Tooltip title='Thumbnail'>
                <IconButton onClick={() => handleOnThumbnail()}>
                  <ViewCarousel />
                </IconButton>
              </Tooltip>
            </div> */}
          </div>
          <div className={classes['drawer-body']}>
            {/* {isThumbnail === true ? ( */}
            <div className={classes['thumbnail-container']} style={{ width: orientation().width }}>
              <GridList cellHeight={orientation().height} cols={1} className={classes['grid-list']}>
                {tile() &&
                  tile().data.map((item, index) => (
                    <GridListTile style={{ border: '0.5px solid black', marginTop: '5%' }}>
                      <Link onClick={() => setPageNumber(indexToPages(index))} className={classes.link}>
                        {index === 0 ? 1 : index > 0 ? indexPage++ : index}
                        {item}
                      </Link>
                    </GridListTile>
                  ))}
              </GridList>
            </div>
            {/* // ) : ( // '' // )} */}
            {/* {isBookmark === true ? (
              <div className={classes['bookmark-container']} style={{ width: orientation().width }}>
                <GridList cellHeight={70} cols={1} className={classes['grid-list']}>
                  {bookmark &&
                    bookmark.map((item, index) => (
                      <GridListTile>
                        <Link onClick={() => setPageNumber(indexToPages(index))} className={classes.link}>
                          <Typography style={{ fontWeight: 'bold' }}>
                            * {item.title}
                          </Typography>
                        </Link>
                      </GridListTile>
                    ))}
                </GridList>
              </div>
            ) : (
              ''
            )} */}
          </div>
        </div>
      </Drawer>
    </div>
  )
}
