import instance from './instance'
// import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axiosClientSOA = instance('/b/s')
const axiosClientJournal = instance('/b/ctl/')
const axiosCreditMemo = instance('/b/cm')
const axiosDebitMemo = instance('/b/dm')
const axiosInvoiceTransaction = instance('/b/i')

export default class ExportsServices {
  static getClientSOAExports (params = {}, { title }) {
    return axiosClientSOA({
      url: `/billing/client/soa-export/${title}`,
      method: 'GET',
      params,
      headers: { Accept: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' },
      responseType: 'blob'
      //   headers: { [ExcludeErrorConstants['404']]: 1 }
    })
  }

  static getCreditMemoExports (params = {}, { title }) {
    return axiosCreditMemo({
      url: `/billing/credit-memo-export/${title}`,
      method: 'GET',
      params,
      headers: { Accept: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' },
      responseType: 'blob'
    })
  }

  static getDebitMemoExports (params = {}, { title }) {
    return axiosDebitMemo({
      url: `/billing/debit-memo-export/${title}`,
      method: 'GET',
      params,
      headers: { Accept: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' },
      responseType: 'blob'
    })
  }

  static getInvoiceExports (params = {}, { title }) {
    return axiosInvoiceTransaction({
      url: `/billing/client/invoice-export/${title}`,
      method: 'GET',
      params,
      headers: { Accept: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' },
      responseType: 'blob'
    })
  }

  static getClientJournalExports (params = {}, { tile, type }) {
    return axiosClientJournal({
      url: `/billing/client/journal-export/${type}/${tile}`,
      method: 'POST',
      data: params,
      headers: { Accept: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' },
      responseType: 'blob'
      //   headers: { [ExcludeErrorConstants['404']]: 1 }
    })
  }

  static getAttachment (soaNumber) {
    return axiosClientSOA({
      url: `/billing/client/export-attachment?soaNumber=${soaNumber}`,
      method: 'GET',
      responseType: 'blob',
      headers: { Accept: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' }
    })
  }
}
