import React from 'react'
import {
  IconButton,
  Tooltip,
  Checkbox,
  Typography,
  InputBase,
  Paper,
  Select,
  MenuItem,
  Button
} from '@material-ui/core'
import {
  Search,
  Maximize,
  HighlightOff,
  ReplyRounded,
  ZoomIn,
  TrackChanges,
  ZoomOut,
  Fullscreen,
  FullscreenExit,
  Print,
  Save,
  ViewCarousel
} from '@material-ui/icons'
import { Link } from 'react-router-dom'
import NumberFormat from 'react-number-format'

// Styles
import classes from '../PdfViewer.module.scss'

export default function Toolbar (props) {
  const {
    clickDrawer,
    toggleDrawer,
    download,
    RBAC,
    handlePrint,
    isBIR,
    click,
    handlePrintBIR,
    ChangePage,
    handleZoomSelection,
    magnifier,
    scale,
    setScale,
    numPages,
    pageNumber,
    setPageNumber,
    handleChangeOnCheckBoxMatchWholeWord,
    handleOnSearch,
    fileName,
    search,
    getText,
    setSearchBarText,
    eraseSearch,
    setMagnifier,
    matchCase,
    handleChangeOnCheckBoxMatchCase,
    matchWholeWord,
    setFullscreen,
    handleOnCloseSearch,
    setSearch,
    setSearchText,
    setMatchCase,
    setMatchWholeWord,
    setGetText,
    isFullscreen,
    exitFullscreen
  } = props

  return (
    <div className={classes.controls}>
      <div className={classes['left-pane']}>
        <Typography className={classes['file-name']} align='left'>
          {fileName}
        </Typography>
        <Tooltip title='Search' className={classes['control-left']}>
          <IconButton
            onClick={() =>
              handleOnSearch({
                setSearchText: setSearchText,
                setSearch: setSearch
              })
            }
          >
            <Search style={{ transform: 'scaleX(-1)', color: 'white' }} />
          </IconButton>
        </Tooltip>
        {search ? (
          <div className={classes['search-span']}>
            <Tooltip title='Search'>
              <Paper className={classes.paper}>
                <div style={{ float: 'left' }}>
                  <InputBase
                    placeholder='Search Keyword...'
                    onChange={e => setSearchBarText(e.target.value)}
                    value={getText}
                    style={{ borderBottom: 'none', padding: '10px' }}
                  />
                </div>
                <div style={{ marginLeft: 'auto' }}>
                  <Link>
                    <Typography>
                      <IconButton onClick={() => eraseSearch({ setSearchText: setSearchText, setGetText: setGetText })}>
                        <HighlightOff style={{ color: 'black' }} />
                      </IconButton>
                    </Typography>
                  </Link>
                </div>
              </Paper>
            </Tooltip>
            <div className={classes.checkbox}>
              <div className={classes['match-case']}>
                <Checkbox
                  checked={matchCase}
                  onClick={() => handleChangeOnCheckBoxMatchCase({ matchCase: matchCase, setMatchCase: setMatchCase })}
                  name='Match Case'
                  indeterminate
                />
                <Typography className={classes['match-case-label']}>Match Case</Typography>
              </div>
              <div className={classes['match-whole-word']}>
                <Checkbox
                  checked={matchWholeWord}
                  onClick={() =>
                    handleChangeOnCheckBoxMatchWholeWord({
                      matchWholeWord: matchWholeWord,
                      setMatchWholeWord: setMatchWholeWord
                    })
                  }
                  name='Match Whole Word'
                  indeterminate
                />
                <Typography className={classes['match-whole-word-label']}>Match Whole Word</Typography>
              </div>
              <div className={classes.close}>
                <div className={classes['close-container']}>
                  <Button
                    className={classes['close-button']}
                    onClick={() =>
                      handleOnCloseSearch({
                        setSearchText: setSearchText,
                        setGetText: setGetText,
                        setSearch: setSearch
                      })
                    }
                  >
                    Close
                  </Button>
                </div>
              </div>
            </div>
          </div>
        ) : (
          ''
        )}
        <Tooltip title='Previous Page' className={classes.control}>
          <IconButton onClick={() => setPageNumber(pageNumber > 1 ? pageNumber - 1 : pageNumber)}>
            <ReplyRounded style={{ color: 'white', transform: 'rotate(270deg) scaleX(-1)' }} />
          </IconButton>
        </Tooltip>
        <Tooltip title='' className={classes.bar}>
          <Maximize style={{ fontSize: '24px', paddingTop: '-10px', color: 'gray', transform: 'rotate(-90deg)' }} />
        </Tooltip>
        <Tooltip title='Next Page' className={classes.back}>
          <IconButton onClick={() => setPageNumber(pageNumber < numPages ? pageNumber + 1 : pageNumber)}>
            <ReplyRounded style={{ color: 'white', transform: 'rotate(-270deg) scaleX(-1)' }} />
          </IconButton>
        </Tooltip>
        <NumberFormat
          value={pageNumber}
          className={classes.input}
          onChange={event =>
            ChangePage({ event: event.target.value, numPages: numPages, setPageNumber: setPageNumber })
          }
        />
        <Typography className={classes['number-pages']}>of {numPages}</Typography>
      </div>
      <div className={classes['middle']}>
        <Tooltip title='Zoom out' className={classes.zoomout}>
          <IconButton onClick={() => (scale === 0.6 ? '' : setScale(scale - 0.1))}>
            <ZoomOut style={{ transform: 'scale(1.2)', color: 'white' }} />
          </IconButton>
        </Tooltip>
        {/* <Button style={{ backgroundColor: '#4f4f4f' }} onClick={() => onClickEvent(scale)}>
          Automatic Zoom
          <UnfoldMore style={{ color: 'white' }} />
      </Button> */}
        <Select
          placeholder={'Automatic Zoom'}
          value={magnifier}
          onChange={event =>
            handleZoomSelection({
              event: event,
              setScale: setScale,
              setMagnifier: setMagnifier
            })
          }
          className={classes.autozoom}
        >
          <MenuItem value={'Actual Size'}>Actual Size</MenuItem>
          <MenuItem value={'Page Fit'}>Page Fit</MenuItem>
          <MenuItem value={'Page Width'}>Page Width</MenuItem>
          <MenuItem value={'50%'}>50%</MenuItem>
          <MenuItem value={'75%'}>75%</MenuItem>
          <MenuItem value={'100%'}>100%</MenuItem>
          <MenuItem value={'125%'}>125%</MenuItem>
          <MenuItem value={'150%'}>150%</MenuItem>
          <MenuItem value={'200%'}>200%</MenuItem>
          <MenuItem value={'300%'}>300%</MenuItem>
          <MenuItem value={'400%'}>400%</MenuItem>
        </Select>
        <Tooltip title='Zoom in' className={classes.zoom}>
          <IconButton onClick={() => setScale(scale + 0.1)}>
            <ZoomIn style={{ transform: 'scale(1.2)', color: 'white' }} />
          </IconButton>
        </Tooltip>
      </div>
      <div className={classes['far-right']}>
        {isFullscreen === true ? (
          <Tooltip title='Exit Fullscreen' className={classes.fullscreen}>
            <IconButton onClick={exitFullscreen}>
              <FullscreenExit style={{ color: 'white' }} />
            </IconButton>
          </Tooltip>
        ) : (
          <Tooltip title='Fullscreen' className={classes.fullscreen}>
            <IconButton onClick={() => setFullscreen(true)}>
              <Fullscreen style={{ color: 'white' }} />
            </IconButton>
          </Tooltip>
        )}
        {isBIR === true ? (
          <Tooltip title='To Print BIR' className={classes['to-print-bir ']}>
            <IconButton onClick={() => handlePrintBIR(click)}>
              <TrackChanges style={{ color: 'white' }} />
            </IconButton>
          </Tooltip>
        ) : (
          ''
        )}
        {RBAC.hasPolicy({ or: ['BC-012', 'BC-015', 'BC-067', 'BC-088'] }) && (
          <Tooltip title='Print' className={classes.print}>
            <IconButton onClick={() => handlePrint()}>
              <Print style={{ color: 'white' }} />
            </IconButton>
          </Tooltip>
        )}

        <Tooltip title='Download' className={classes.download}>
          <IconButton onClick={download}>
            <Save style={{ color: 'white' }} />
          </IconButton>
        </Tooltip>

        <Tooltip title='Thumbnail' className={classes.bookmark}>
          <IconButton onClick={() => toggleDrawer(clickDrawer)}>
            <ViewCarousel style={{ color: 'white' }} />
          </IconButton>
        </Tooltip>
      </div>
    </div>
  )
}
