import instance from '../../../instance'

const axios = instance('/b/i')

export default class InvoiceViewServices {
  // TODO old getTobeRevisedInvoice
  static getInvoiceByTransactionNo ({ transactionNumber }) {
    return axios({
      url: `/billing/invoice/${transactionNumber}`,
      method: 'GET'
    })
  }

  // TODO old getInvoiceDetails
  static getInvoiceDetails (params) {
    return axios({
      url: `/billing/invoice-details/${params.transactionNumber}`,
      method: 'GET',
      params,
      headers: { skip_generate_form_data: 1 }
    })
  }
}
