import React from 'react'
// import Paper from '@material-ui/core/Paper'
import Submit from 'StandardComponents/Submit/Submit'
import { useHistory } from 'react-router-dom'

// Components
import PdfViewer from 'StandardComponents/PdfViewer/PdfViewer'
import Approve from 'StandardComponents/Approve/Approve'

// Service
import BillingSOAReportService from 'Services/Api/Billing/SOA/BillingSOAReportService'
import { soaAttachmentExport } from '../ExportTools/index'

// import BillingAnalystServices from 'Services/Api/Billing/AccountSetup/BillingAnalystServices'
import BillingSupervisorServices from 'Services/Api/Billing/AccountSetup/BillingSupervisorServices'
import BillingSOAServices from 'Services/Api/Billing/SOA/BillingSOAServices'
import classes from './Attachment.module.scss'

export default function Attachment (props) {
  const { soaNumber, submit = {}, refContainer } = props
  const [file, setFile] = React.useState(null)
  const [number, setNumber] = React.useState(soaNumber)
  const [openDialog, setOpenDialog] = React.useState(true)
  const isLandscape = true
  const history = useHistory()
  const type = 'list'
  React.useEffect(() => {
    if (submit.soaType === 'member') {
      BillingSOAReportService.getMemberReport({ type: type, soaNumber: soaNumber }).then(response => {
        const blob = new Blob([response.data])
        setFile(blob)
        setNumber(soaNumber)
      })
    } else {
      BillingSOAReportService.getReport({ type: type, soaNumber: soaNumber }).then(response => {
        const blob = new Blob([response.data])
        setFile(blob)
        setNumber(soaNumber)
      })
    }
  }, [type])

  const approveFunction = async args => {
    const { soaNumber, soaType } = args
    if (soaType === 'member') {
      await BillingSOAServices.assignMemberSOA({ soaNumber: soaNumber })
      await BillingSOAServices.routeMemberSOA({ soaNumber: soaNumber, action: 'approve' }).then(res => {
      })
    } else {
      await BillingSupervisorServices.assignClientSOA({ soaNumber: soaNumber })
      await BillingSupervisorServices.routeSOA({ soaNumber: soaNumber, action: 'approve' }).then(res => {
      })
    }
    history.push('/')
  }

  const disapproveFunction = async args => {
    const { soaNumber, soaType } = args
    if (soaType === 'member') {
      await BillingSOAServices.assignMemberSOA({ soaNumber: soaNumber })
      await BillingSOAServices.routeMemberSOA({ soaNumber: soaNumber, action: 'disapprove' }).then(res => {
      })
    } else {
      await BillingSupervisorServices.assignClientSOA({ soaNumber: soaNumber })
      await BillingSupervisorServices.routeSOA({ soaNumber: soaNumber, action: 'disapprove' }).then(res => {
      })
    }
    history.push('/')
  }

  const submitFunction = async args => {
    const { soaNumber, soaType } = args
    if (soaType === 'member') {
      await BillingSOAServices.assignMemberSOA({ soaNumber: soaNumber })
      await BillingSOAServices.routeMemberSOA({ soaNumber: soaNumber, action: 'submit' }).then(res => {
      })
    } else {
      await BillingSupervisorServices.assignClientSOA({ soaNumber: soaNumber })
      await BillingSupervisorServices.routeSOA({ soaNumber: soaNumber, action: 'submit' }).then(res => {
      })
    }
    history.push('/')
  }

  return (
    <div className={classes.root}>
      {submit.type === 'supervisor' ? (
        <div>
          {file ? (
            <PdfViewer
              isLandscape={isLandscape}
              file={file}
              download={() => soaAttachmentExport(soaNumber)}
            />
          ) : (
            ''
          )}
          {submit.activeStep && (
            <Approve
              page='soa'
              openDialog={openDialog}
              setOpenDialog={setOpenDialog}
              submitFunction={() =>
                approveFunction({ soaNumber: number })
              }
              disapproveFunction={() =>
                disapproveFunction({ soaNumber: number })
              }
              setActiveStep={submit.setActiveStep}
            />
          )}
        </div>
      ) : (
        <div>
          {file ? (
            <PdfViewer
              isLandscape={isLandscape}
              file={file}
              download={() => soaAttachmentExport(soaNumber)}
            />
          ) : (
            ''
          )}
          {submit.activeStep && (
            <Submit
              page='soa'
              openDialog={openDialog}
              setOpenDialog={setOpenDialog}
              submitFunction={() =>
                submitFunction({ soaNumber: number })
              }
              onCloseDialog={() => {
                if (refContainer !== undefined) {
                  refContainer.current.parentElement.scrollTo(0, 0)
                }
              }}
              setActiveStep={submit.setActiveStep}
            />
          )}
        </div>
      )}
      {submit.type === 'validated' ? (
        <div>
          {file ? (
            <PdfViewer
              isLandscape={isLandscape}
              file={file}
              download={() => soaAttachmentExport(soaNumber)}
            />
          ) : (
            ''
          )}
          {submit.activeStep && history.push('/')}
        </div>
      ) : (
        ''
      )}
      {submit.type === 'submitted' ? (
        <div>
          {file ? (
            <PdfViewer
              isLandscape={isLandscape}
              file={file}
              download={() => soaAttachmentExport(soaNumber)}
            />
          ) : (
            ''
          )}
          {submit.activeStep && history.push('/')}
        </div>
      ) : (
        ''
      )}
      {submit.type === 'analyst-approved' ? (
        <div>
          {file ? (
            <PdfViewer
              isLandscape={isLandscape}
              file={file}
              download={() => soaAttachmentExport(soaNumber)}
            />
          ) : (
            ''
          )}
          {submit.activeStep && history.push('/')}
        </div>
      ) : (
        ''
      )}
    </div>
  )
}
