import React from 'react'
import { Document, Page, pdfjs, Outline } from 'react-pdf'
import Fullscreen from './components/Fullscreen'
import { useReactToPrint } from 'react-to-print'
import CssBaseline from '@material-ui/core/CssBaseline'

// Styles
import classes from './PdfViewer.module.scss'

// Components
import BookMarkDrawer from './components/BookmarkDrawer'
import Toolbar from './components/Toolbar'

// Services
import handleZoomSelection from './services/handleZoomSelection'
import {
  makeTextRenderer,
  handleOnSearch,
  searchLogic,
  handleChangeOnCheckBoxMatchCase,
  handleChangeOnCheckBoxMatchWholeWord,
  eraseSearch,
  handleOnCloseSearch
} from './services/handleSearch'
import ChangePage from './services/ChangePage'

// Security
import RBAC from 'Security/RBAC'

pdfjs.GlobalWorkerOptions.workerSrc = `https://cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`

export default function PdfViewer (props) {
  const {
    rotate,
    file,
    isBIR = false,
    fileName,
    initialPageNumber = 1,
    initialScale = 1.3,
    download = () => {},
    isLandscape,
    alternateFile,
    click,
    handlePrintBIR,
    isFullscreen,
    exitFullscreen,
    mountedRef = null
  } = props
  const [scale, setScale] = React.useState(initialScale)
  const [pageNumber, setPageNumber] = React.useState(initialPageNumber)
  const [numPages, setNumPages] = React.useState(0)
  const componentRef = React.useRef()
  const [search, setSearch] = React.useState(false)
  const [searchText, setSearchText] = React.useState('')
  const [anchor, setAnchor] = React.useState(false)
  const [clickDrawer, setClickDrawer] = React.useState(false)
  const [magnifier, setMagnifier] = React.useState('100%')
  const [searchBarText, setSearchBarText] = React.useState('')
  const [matchCase, setMatchCase] = React.useState(false)
  const [matchWholeWord, setMatchWholeWord] = React.useState(false)
  const [getText, setGetText] = React.useState('')
  const [fullscreen, setFullscreen] = React.useState(false)
  // const [bookmark, setBookmark] = React.useState(null)
  // const [getBookmarkPage, setBookmarkPage] = React.useState(pageNumber)
  const handlePrint = useReactToPrint({
    content: () => componentRef.current
  })

  React.useImperativeHandle(mountedRef, () => ({
    callbackPrint() {
      handlePrint()
    }
  }))

  React.useEffect(() => {
    setPageNumber(1)
    setNumPages(1)
  }, [file])

  React.useEffect(() => {
    searchLogic({
      setGetText: setGetText,
      searchBarText: searchBarText,
      setSearchText: setSearchText,
      matchCase: matchCase,
      matchWholeWord: matchWholeWord
    })
  }, [searchBarText, matchCase, matchWholeWord])

  const toggleDrawer = clickDrawer => {
    if (clickDrawer === false) {
      setAnchor(true)
      setClickDrawer(true)
    } else {
      setAnchor(false)
      setClickDrawer(false)
    }
  }

  return (
    <div className={classes.root}>
      <div className={classes.drawer}>
        <CssBaseline />
        <BookMarkDrawer
          anchor={anchor}
          alternateFile={alternateFile}
          file={file}
          setPageNumber={setPageNumber}
          isLandscape={isLandscape}
          setNumPages={setNumPages}
          pageNumber={pageNumber}
          scale={scale}
          // bookmark={bookmark}
          rotate={rotate}
          numPages={numPages}
          // getBookmarkPage={getBookmarkPage}
        />
      </div>
      <div className={classes.pdf} id={'document'}>
        <Document
          inputRef={componentRef}
          file={alternateFile || file}
          onLoadSuccess={document => {
            setNumPages(document.numPages)
          }}
        >
          {/* <Outline
            className={classes.outline}
            onLoadError={e => {
              console.log('error', e)
            }}
            onLoadSuccess={array => console.log(array)}
            onItemClick={pageNumber => console.log(pageNumber)}
            onParseSuccess={({ outline }) => alert('There are ' + outline.length + ' top level items in the table of contents.')}
          /> */}
          <Page
            pageNumber={pageNumber}
            customTextRenderer={makeTextRenderer(searchText)}
            scale={scale}
            rotate={rotate}
          />
        </Document>
      </div>
      <div className={classes['toolbar']}>
        <Toolbar
          isFullscreen={isFullscreen}
          clickDrawer={clickDrawer}
          toggleDrawer={toggleDrawer}
          download={download}
          RBAC={RBAC}
          handlePrint={handlePrint}
          isBIR={isBIR}
          click={click}
          handlePrintBIR={handlePrintBIR}
          setFullscreen={setFullscreen}
          exitFullscreen={exitFullscreen}
          ChangePage={ChangePage}
          handleZoomSelection={handleZoomSelection}
          magnifier={magnifier}
          setMagnifier={setMagnifier}
          scale={scale}
          setScale={setScale}
          numPages={numPages}
          pageNumber={pageNumber}
          setPageNumber={setPageNumber}
          handleChangeOnCheckBoxMatchWholeWord={handleChangeOnCheckBoxMatchWholeWord}
          handleOnSearch={handleOnSearch}
          fileName={fileName}
          search={search}
          setGetText={setGetText}
          getText={getText}
          searchBarText={searchBarText}
          setSearchText={setSearchText}
          setSearchBarText={setSearchBarText}
          eraseSearch={eraseSearch}
          matchCase={matchCase}
          handleOnCloseSearch={handleOnCloseSearch}
          handleChangeOnCheckBoxMatchCase={handleChangeOnCheckBoxMatchCase}
          matchWholeWord={matchWholeWord}
          setMatchCase={setMatchCase}
          setMatchWholeWord={setMatchWholeWord}
          setSearch={setSearch}
        />
      </div>
      {fullscreen ? (
        <Fullscreen
          downloadFullscreen={download}
          isBIR={isBIR}
          open={fullscreen}
          isLandscape={isLandscape}
          close={() => setFullscreen(false)}
          file={file}
          handlePrintBIR={handlePrintBIR}
          click={click}
          alternateFile={alternateFile}
        />
      ) : (
        ''
      )}
    </div>
  )
}
