import React from 'react'
// Components
import PdfViewer from 'StandardComponents/PdfViewer/PdfViewer'

// Service
import BillingSOAReportService from 'Services/Api/Billing/SOA/BillingSOAReportService'

import classes from './BIR.module.scss'

export default function BIR (props) {
  const { soaNumber, soaType } = props
  const [file, setFile] = React.useState(null)
  const isBIR = true
  const [alternateFile, setAlternateFile] = React.useState(null)
  const [click, setClick] = React.useState(false)
  const type = 'preview'
  React.useEffect(() => {
    if (soaType === 'member') {
      BillingSOAReportService.getMemberReport({ type: type, soaNumber: soaNumber }).then(response => {
        const blob = new Blob([response.data])
        setFile(blob)
      })
    } else {
      BillingSOAReportService.getReport({ type: type, soaNumber: soaNumber }).then(response => {
        const blob = new Blob([response.data])
        setFile(blob)
      })
    }
  }, [type])
  const downloadServices = args => {
    const { type, soaType } = args
    if (soaType === 'member') {
      BillingSOAReportService.getMemberReport({ type: type, soaNumber: soaNumber }).then(response => {
        const url = URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', 'soa-bir.pdf')
        document.body.appendChild(link)
        link.click()
      })
    } else {
      BillingSOAReportService.getReport({ type: type, soaNumber: soaNumber }).then(response => {
        const url = URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', `soa-bir-${soaNumber}.pdf`)
        document.body.appendChild(link)
        link.click()
      })
    }
  }

  const handlePrintBIR = async click => {
    if (click === false) {
      await BillingSOAReportService.getReport({ type: 'print', soaNumber: soaNumber }).then(response => {
        setAlternateFile(response.data)
      })
      setClick(true)
    } else {
      setAlternateFile(file)
      setClick(false)
    }
  }

  return (
    <div className={classes.root}>
      {file ? (
        <PdfViewer
          file={file}
          alternateFile={alternateFile}
          isBIR={isBIR}
          handlePrintBIR={handlePrintBIR}
          click={click}
          download={() => downloadServices({ type: 'preview', soaType: soaType })}
        />
      ) : (
        ''
      )}
    </div>
  )
}
