import React from 'react'
import Chart from 'react-apexcharts'
import Typography from '@material-ui/core/Typography'

// Tile
import DashboardTile from '../../../DashboardTileComponent/DashboardTileComponent'

// Services
import CollectionService from 'Services/Api/CollectionCDFServices'
import { useHistory } from 'react-router-dom'

// Style
import classes from './style.module.scss'

const ForecastChartTile = props => {
  const history = useHistory()
  const [tile, setTile] = React.useState({
    filterMenu: {
      data: [
        { value: 'previous-month', text: 'Previous Month' },
        { value: 'current-month', text: 'This Month' },
        { value: 'next-month', text: 'Next Month' }
      ],
      default: 'current-month'
    },
    user_owned: true,
    results: []
  })

  React.useEffect(() => {
    Promise.all([
      CollectionService.getCollectionForecastSummary({
        range: tile.filterMenu.default
      })
    ]).then(res => {
      const [CDFService] = res
      const { results, title } = CDFService.data.data
      setTile(i => ({ ...i, results, title }))
    }).catch(err => console.error(`Error fetching Reports: ${err}`))

  }, [tile.filterMenu.default])

  const handleOnViewAll = () => {}

  return (
    <React.Fragment>
      <DashboardTile
        id='aging-summary-tile'
        count={{ color: 20, value: tile.title || '', id: 'collection-forecast-tile-title' }}
        sizes={{ sm: 12 }}
        title=''
        isFilterShown
        filterMenu={{ ...tile.filterMenu }}
        onFilterChange={e => setTile(i => ({ ...i, filterMenu: { ...i.filterMenu, default: e } }))}
        headerSize={'h6'}
        countSize={'h5'}
      >
        <div className={classes.root} onClick={() => { history.push('/reports') }}>
          <Chart
            type='bar'
            width='100%'
            height='100%'
            options={{
              chart: {
                toolbar: {
                  show: false
                }
              },
              legend: {
                show: true,
                position: 'top',
                horizontalAlign: 'right',
                showForSingleSeries: true,
                showForZeroSeries: true
              },
              dataLabels: { enabled: false },
              xaxis: { categories: tile.results.map(i => { return i.name }) }
            }}
            series={[{ name: 'Collection Forecast', data: tile.results.map(i => { return i.amount }) }]}
          />
        </div>
      </DashboardTile>
    </React.Fragment>
  )
}

ForecastChartTile.displayName = 'DashboardTileComponent'

export default ForecastChartTile
