const Transition = React.forwardRef(function Transition (props, ref) {
  return <Slide direction='up' ref={ref} {...props} />
})

export default function Fullscreen (props) {
  const { close, downloadFullscreen, open, isLandscape, file, isBIR, handlePrintBIR, click, alternateFile } = props
  // const classes = useStyles()
  const isFullscreen = true
  return (
    <div>
      <Dialog fullScreen open={open} onClose={close} TransitionComponent={Transition}>
        {/* <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton edge='start' color='inherit' onClick={close} aria-label='close'>
              <CloseIcon />
            </IconButton>
            <Typography variant='h6' className={classes.title}>
              Preview Sheet
            </Typography>
          </Toolbar>
        </AppBar> */}
        <DialogContent style={{ padding: '0px' }}>
          {file ? (
            <PdfViewer
              isLandscape={isLandscape}
              file={file}
              download={downloadFullscreen}
              isBIR={isBIR}
              alternateFile={alternateFile}
              handlePrintBIR={handlePrintBIR}
              click={click}
              exitFullscreen={close}
              isFullscreen={isFullscreen}
            />
          ) : (
            ''
          )}
        </DialogContent>
      </Dialog>
    </div>
  )
}
