export default function ChangePage (props) {
  const { event, numPages, setPageNumber } = props
  if (numPages === 1) {
    setPageNumber(1)
  } else {
    if (!event) {
      setPageNumber(1)
    } else {
      if (event > numPages) {
        setPageNumber(numPages)
      } else {
        setPageNumber(parseInt(event))
      }
    }
  }
}
