import React from 'react'

const searchLogic = props => {
  const { setGetText, searchBarText, setSearchText, matchCase, matchWholeWord } = props
  setGetText(searchBarText)
  if (searchBarText === '') {
    setSearchText('')
  } else {
    if (matchCase) {
      const query = new RegExp(searchBarText, 'gm')
      setSearchText(query)
    }
    if (matchWholeWord) {
      const query = new RegExp('(\\b' + searchBarText + '\\b)', 'gim')
      setSearchText(query)
    }
    if (!matchCase && !matchWholeWord) {
      const query = new RegExp(searchBarText, 'gim')
      setSearchText(query)
      setSearchText(query)
    }
    if (matchCase && matchWholeWord) {
      const query = new RegExp(searchBarText, 'gm')
      setSearchText(query)
      setSearchText(query)
    }
  }
}

const handleOnSearch = props => {
  const { setSearch, setSearchText } = props
  setSearchText('')
  setSearch(true)
}

const handleChangeOnCheckBoxMatchCase = props => {
  const { matchCase, setMatchCase } = props
  if (matchCase === false) {
    setMatchCase(true)
  } else {
    setMatchCase(false)
  }
}

const handleChangeOnCheckBoxMatchWholeWord = props => {
  const { matchWholeWord, setMatchWholeWord } = props
  if (matchWholeWord === false) {
    setMatchWholeWord(true)
  } else {
    setMatchWholeWord(false)
  }
}

const eraseSearch = props => {
  const { setSearchText, setGetText } = props
  setSearchText('')
  setGetText('')
}

const handleOnCloseSearch = props => {
  const { setSearch, setSearchText, setGetText } = props
  setSearch(false)
  eraseSearch({ setSearchText: setSearchText, setGetText: setGetText })
}

const highlightPattern = (text, pattern) => {
  const splitText = text.split(pattern)

  if (splitText.length <= 1) {
    return text
  }
  const matches = text.match(pattern)
  return splitText.reduce(
    (arr, element, index) =>
      matches[index]
        ? [...arr, element, <mark style={{ marginLeft: '2px', fontSize: '14px' }}>{matches[index]}</mark>]
        : [...arr, element],
    []
  )
}

const makeTextRenderer = searchText => textItem => highlightPattern(textItem.str, searchText)

export {
  makeTextRenderer,
  searchLogic,
  handleChangeOnCheckBoxMatchCase,
  handleChangeOnCheckBoxMatchWholeWord,
  eraseSearch,
  handleOnCloseSearch,
  handleOnSearch
}
