import React from 'react'
import moment from 'moment'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'

import { Popper, IconButton, Fade, Grid } from '@material-ui/core'
import AccountBoxIcon from '@material-ui/icons/AccountBox'

import classes from './TreeViewTable.module.scss'
import { Link } from 'react-router-dom'

import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

export default function NormalRow (props) {
  const {
    index,
    table_columns,
    data,
    id,
    to,
    param,
    actionTo,
    style,
    show,
    monetary_columns = [],
    emphasis_columns = [],
    date_columns = [],
    multipleActions,
    editAccessor,
    coloredRow
  } = props
  const [anchorEl, setAnchorEl] = React.useState(null)
  const open = Boolean(anchorEl)

  const handleClick = event => {
    if (event.target.value === 'x') {
      setAnchorEl(null)
    } else {
      setAnchorEl(anchorEl ? null : event.currentTarget)
    }
  }

  return (
    <React.Fragment>
      <TableRow key={index} id={id} tabIndex={index}>
        <TableCell
          id={`account-id-${index}`}
          className={[
            classes.rowCollapseButton,
            editAccessor && data[editAccessor] && data[editAccessor] === coloredRow ? classes.editRow : ''
          ]}
        />

        {table_columns.map((column, iteration) => (
          <TableCell
            className={[
              classes.contentCell,
              editAccessor && data[editAccessor] && data[editAccessor] === coloredRow ? classes.editRow : ''
            ]}
            style={emphasis_columns.includes(column) === true ? { 'font-weight': 'bold' } : { 'font-weight': 'normal' }}
          >
            {(data[column] === null) | (data[column] === undefined)
              ? ''
              : monetary_columns.includes(column) === true
              ? 'Php ' + CurrencyFormatter(data[column])
              : date_columns.includes(column) === true
              ? DateFormatterByAbbreviation(data[column])
              : data[column]}
          </TableCell>
        ))}
        {show && (
          <TableCell
            className={[
              classes.contentCell,
              editAccessor && data[editAccessor] && data[editAccessor] === coloredRow ? classes.editRow : ''
            ]}
          >
            <IconButton>
              <AccountBoxIcon onClick={handleClick} />
              <Popper
                id={id}
                open={open}
                anchorEl={anchorEl}
                transition
                style={{ 'z-index': '1301', width: '160px', marginLeft: '20px' }}
                placement={'right-end'}
              >
                {({ TransitionProps }) => (
                  <Fade {...TransitionProps} timeout={350}>
                    <div
                      style={{
                        padding: '10px',
                        'background-color': 'white',
                        'box-shadow': '5px 5px rgb(0,0,0,10%)'
                      }}
                    >
                      <Grid container spacing={2}>
                        <Grid item xs={10}>
                          <Link
                            id
                            to={`/clients/assign/${data._id}/${data.corporate_id}?reAssign=true`}
                            className={classes.link}
                            style={{ marginLeft: '10px', textDecoration: 'underline' }}
                          >
                            Billing Analyst
                          </Link>
                        </Grid>
                        <Grid
                          item
                          xs={2}
                          style={{ 'font-size': '15px', cursor: 'pointer', float: 'right' }}
                          onClick={e => handleClick(e)}
                        >
                          X
                        </Grid>
                        <Grid item xs={12}>
                          <Link
                            id
                            to={`/collection/assign/${data._id}/${data.corporate_id}?reAssign=true`}
                            className={classes.link}
                            style={{ marginLeft: '10px', textDecoration: 'underline' }}
                          >
                            Collection Analyst
                          </Link>
                        </Grid>
                      </Grid>
                    </div>
                  </Fade>
                )}
              </Popper>
            </IconButton>
          </TableCell>
        )}
        {actionTo ? (
          <TableCell
            className={[
              classes.contentCell,
              editAccessor && data[editAccessor] && data[editAccessor] === coloredRow ? classes.editRow : ''
            ]}
          >
            <Link id to={param == null ? actionTo : actionTo.concat('/', data[param])} className={classes.link}>
              View
            </Link>
          </TableCell>
        ) : null}
        {multipleActions ? (
          <TableCell
            className={[
              classes.contentCell,
              editAccessor && data[editAccessor] && data[editAccessor] === coloredRow ? classes.editRow : ''
            ]}
          >
            {multipleActions.map((action, iteration) => (
              <Link
                id
                onClick={() => action.handleClick(data, data[editAccessor])}
                className={classes.link}
                style={{ textDecoration: 'underline', marginRight: '10px' }}
              >
                {action.label}
              </Link>
            ))}
          </TableCell>
        ) : null}
      </TableRow>
    </React.Fragment>
  )
}
