import ExportsServices from 'Services/Api/ExportsServices'
import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

const handleExportCreditMemo = async args => {
  const { list, tableHeaders, transactionNumber, title } = args
  const fields = []
  const headers = []
  for (const { billingCodeType, memberAccountId, memberName, description, taxType, taxableAmount } of list.body) {
    await fields.push({
      field_1: billingCodeType,
      field_2: memberAccountId,
      field_3: memberName,
      field_4: description,
      field_5: taxType,
      field_6: taxableAmount
    })
  }
  for (const { label } of tableHeaders) {
    await headers.push(label)
  }
  const data = {
    headers: headers,
    fields: fields
  }
  ExportsServices.getCreditMemoExports(data, { title: title }).then(response => {
    const url = URL.createObjectURL(new Blob([response.data]))
    const link = document.createElement('a')
    link.href = url
    link.setAttribute('download', `${title}-${transactionNumber}.xlsx`)
    document.body.appendChild(link)
    link.click()
  })
}

const ExportBillingSetupTileService = async args => {
  const { list, tableHeaders, title, type } = args
  const fields = []
  const headers = []
  for (const i of list) {
    switch (type) {
      case 'Assign Analyst':
        await fields.push({
          field_1: i.client.account_number,
          field_2: i.client.account_name,
          field_3: i.contract.type,
          field_4: DateFormatterByAbbreviation(i.contract.effectivity)
        })
        break
      case 'Setup Billing Cycle':
        await fields.push({
          field_1: i.client.account_number,
          field_2: i.client.account_name,
          field_3: i.contract.type,
          field_4: DateFormatterByAbbreviation(i.contract.effectivity)
        })
        break
    }
  }
  for (const { label } of tableHeaders) {
    await headers.push(label)
  }
  const data = {
    headers: headers,
    fields: fields
  }
  ExportsServices.getClientSOAExports(data, { title: title }).then(response => {
    const url = URL.createObjectURL(new Blob([response.data]))
    const link = document.createElement('a')
    link.href = url
    link.setAttribute('download', `${title}.xlsx`)
    document.body.appendChild(link)
    link.click()
  })    
}

