export default function handleZoomSelection (props) {
  const { event, setScale, setMagnifier } = props
  switch (event.target.value) {
    case 'Actual Size':
      setScale(1.3)
      break
    case 'Page Fit':
      setScale(1)
      break
    case 'Page Width':
      setScale(3)
      break
    case '50%':
      setScale(0.65)
      break
    case '75%':
      setScale(0.975)
      break
    case '100%':
      setScale(1.3)
      break
    case '125%':
      setScale(1.65)
      break
    case '150%':
      setScale(1.95)
      break
    case '200%':
      setScale(2.6)
      break
    case '300%':
      setScale(3.9)
      break
    case '400%':
      setScale(5.2)
      break
  }
  setMagnifier(event.target.value)
}
